//HERO SLIDER

.hero-slider {
  padding: 50px 0;
  .swiper {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    padding: 45px 80px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background-size: cover;
    border-radius: 10px;
    overflow: hidden;
    min-height: 385px;
    .hero-content-block {
      width: 100%;
      max-width: 55%;

      h2 {
        color: #FFF;
        font-family: 'Roboto', sans-serif;
        font-size: 40px;
        font-style: normal;
        font-weight: 600;
        margin-bottom: 15px;
        line-height: normal;
      }

      p {
        color: #F3F3F3;
        margin-bottom: 0;
        font-family: 'Roboto', sans-serif;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }

      .slider-btn {
        border-radius: 50px;
        background: linear-gradient(107deg, rgba(51, 95, 255, 0.20) 4.64%, rgba(0, 55, 255, 0.20) 34.64%, rgba(255, 255, 255, 0.20) 54.46%, rgba(18, 69, 255, 0.20) 108.41%), #0255C2;
        padding: 16px 60px;
        color: #FFF;
        display: table;
        border: 0;
        text-align: center;
        font-family: 'Roboto', sans-serif;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: capitalize;
        margin-top: 55px;
        text-decoration: none;
        outline: none;
      }
    }
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

